<template>
  <div class="chatThema">
    <div class="chatTxtBox">
      <input v-model="input" v-on:keypress.enter="submit" @focus="onFocus" type="text" :placeholder="placeholder"/>
      <a @click="submit" :class="isOnConsulting ? 'cb-disabled-button' : ''" role="button">검색</a>
    </div>
    <ul class="chatQB" v-show="show_key">
      <li v-for="(item, index) in parsedData" v-bind:key="index"><a @click="click(item)" :class="isOnConsulting ? 'cb-disabled-button' : ''" role="button">#{{item}}</a></li>
    </ul>
  </div>
</template>
<style scoped>
a{
  cursor:pointer;
}
</style>
<script>
import consultingStatus from '@/mixins/consultingStatus';

export default {
  name: 'KeywordSearchAndInfo',
  mixins: [consultingStatus],

  props: {
    data: {},
  },
  data() {
    return {
      input: '',
      show_key: true,
    };
  },
  mounted() {
    this.$uiHandler.swiperNextPrevShow();
  },

  computed: {
    parsedData() {
      return this.data.keywords;
    },
    placeholder() {
      return window.language === this.$dictionary.languageType.KR ? '추천 키워드 선택 및 입력' : '请选择并输入下列推荐的关键词。';
    },
  },
  filters: {
  },
  methods: {
    onFocus() {
      this.input = '';
    },
    click(id) {
      if (this.isOnConsulting) {
        return;
      }

      this.send(id);

      this.input = `#${ id}`;
      this.show_key = false;
      this.$analyticsHandler.apply('temp', { keyword: id, thema: this.data.thema });
    },
    submit() {
      if (this.isOnConsulting) {
        return;
      }

      this.send(this.input);

      this.input = `#${ this.input}`;
      this.show_key = false;
    },
    send(keyword) {
      this.$store.dispatch('SEND_MESSAGE', {
        message: keyword,
        hidden: true,
        options: {
          direct: true,
          from: this.$dictionary.elementType.KEYWORD_SEARCH,
          type: this.data.type,
          buttons: this.data.keywords,
          thema: this.data.thema,
          intent: this.$dictionary.intentType.SA_recommend, // this.data.intent,
        },
      });
    },
  },
};
</script>